.index-screen-2 {
	p {
		margin: 0;
		font-size: 25px;
		line-height: 40px; } }

@include media-breakpoint-down(xxl) {
	.index-screen-2 {
		p {
			margin: 0;
			font-size: 22px;
			line-height: 36px; } } }

@include media-breakpoint-down(xl) {
	.index-screen-2 {
		p {
			margin: 0;
			font-size: 20px;
			line-height: 34px; } } }

@include media-breakpoint-down(lg) {
	.index-screen-2 {
		p {
			margin: 0;
			font-size: 18px;
			line-height: 30px; } } }

@include media-breakpoint-down(md) {
	.index-screen-2 {
		p {
			margin: 0;
			font-size: 16px;
			line-height: 24px; } } }

@include media-breakpoint-down(sm) {
	.index-screen-2 {
		p {
			margin: 0;
			font-size: 16px;
			line-height: 22px; } } }

@include media-breakpoint-down(xs) {
	.index-screen-2 {
		p {
			margin: 0;
			font-size: 16px;
			line-height: 20px; } } }
