.header {
	margin-bottom: 70px;

	&.header_inner {
		margin-bottom: 0;
		padding: {
			top: 40px;
			bottom: 20px; }
		background-color: #13191d;

		.header-nav {
			margin-bottom: 10px; }

		.btn {
			margin-left: 40px; } }

	&-logo {
		max-width: 175px;
		text-decoration: none;
		&__img {
			display: block;
			width: 100%;
			height: auto; } }

	&-language {
		margin: 0 0 25px;
		padding: 0;
		display: flex;
		list-style: none;
		&__li {
			margin-left: 20px; }

		&__link {
			display: block;
			padding: 0 6px;
			box-sizing: border-box;
			text-align: center;
			font-size: 24px;
			line-height: 40px;
			letter-spacing: 1px;
			color: $font-light;
			transition: .25s;
			&:hover {
				color: $font-light;
				text-decoration: none;
				background-color: $accent; }
			&_active {
				background-color: $accent; } } }

	&-nav-list {
		display: flex;
		margin: 0;
		padding: 0;
		list-style: none;
		&__li {
			margin-left: 50px;
			&_active {
				.header-nav-list__link {
					text-decoration: none;
					color: $accent; } } }
		&__link {
			display: block;
			padding: 5px 0;
			box-sizing: border-box;
			font-size: 25px;
			line-height: 29px;
			color: $font-light;
			text-transform: uppercase;
			text-decoration: none;
			transition: .25s;
			&:hover {
				text-decoration: none;
				color: $accent; } } }

	.btn.toggle-mobile-menu {
		position: relative;
		margin: 0;
		display: none;
		outline: none;
		&.toggle-mobile-menu_active {
			color: $accent;
			border-color: $accent;
			.lines {
				transform: rotate(90deg);
				background-color: transparent;
				box-shadow: 0 -6px 0 0 transparent, 0 6px 0 0 transparent;
				&::before,
				&::after {
					background-color: $accent; } } } } }

@include media-breakpoint-down(xxl) {
	.header {

		&.header_inner {
			.btn {
				margin-left: 30px;
				min-height: 50px;
				font-size: 22px;
				line-height: 46px; } }

		&-logo {
			max-width: 125px; }

		&-language__link {
			font-size: 18px; }

		&-nav-list {
			&__li {
				margin-left: 40px; }
			&__link {
				font-size: 18px;
				line-height: 22px; } } } }

@include media-breakpoint-down(xl) {
	.header {
		margin-bottom: 60px;

		&.header_inner {

			padding-top: 25px;

			.header-nav {
				margin-bottom: 0; }

			.btn {
				margin-left: 20px;
				min-height: 34px;
				min-width: 200px;
				font-size: 16px;
				line-height: 30px; } }

		&-logo {
			max-width: 100px; }

		&-language__link {
			font-size: 16px;
			line-height: 30px; }

		&-nav-list {
			&__li {
				margin-left: 30px; }
			&__link {
				font-size: 16px;
				line-height: 20px; } } } }

@include media-breakpoint-down(lg) {
	.header.header_inner {
		.btn {
			margin-left: 20px;
			min-height: 30px;
			min-width: 150px;
			font-size: 14px;
			line-height: 26px; } } }

@include media-breakpoint-down(md) {
	.header {
		margin-bottom: 50px;

		&.header_inner {
			.btn {
				margin-left: 20px;
				min-height: 40px;
				min-width: 120px;
				font-size: 14px;
				line-height: 36px; } }

		&-logo {
			max-width: 80px; }

		&-language {
			margin-bottom: 15px;
			&__li {
				margin-left: 10px; }
			&__link {
				font-size: 16px;
				line-height: 30px; } }

		&-nav-list {
			&__li {
				margin-left: 20px; }
			&__link {
				font-size: 14px;
				line-height: 18px; } } } }

@include media-breakpoint-down(sm) {
	.header {
		margin-bottom: 40px;

		&.header_inner {
			padding-bottom: 15px;
			.btn {
				margin-bottom: 10px; }

			.header-logo {
				margin-bottom: 10px;
				max-width: 150px; } }


		&-logo {
			max-width: 90px;
			width: 100%; }

		&-language {
			margin-bottom: 10px;
			&__li {
				margin-left: 10px; }
			&__link {
				font-size: 16px;
				line-height: 30px; } }

		&-nav-list {
			position: fixed;
			top: -100vh;
			left: 0;
			z-index: 10;
			flex-direction: column;
			align-items: center;
			padding: 60px 20px;
			box-sizing: border-box;
			width: 100%;
			height: 100vh;
			overflow: auto;
			background-color: rgba(0, 0, 0, .8);
			transition: .5s;
			&_active {
				top: 0; }
			&__li {
				margin-bottom: 10px;
				margin-left: 0; }
			&__link {
				font-size: 16px;
				line-height: 20px;
				letter-spacing: 2px; } }

		.btn.toggle-mobile-menu {
			display: block;
			min-height: 34px;
			font-size: 16px;
			line-height: 34px;
			transition: .5s;
			&.toggle-mobile-menu_active {
				position: fixed;
				top: 0;
				right: 0;
				z-index: 15; } } } }

