.index-screen-1 {
	padding: 170px 0 50px;
	min-height: 100vh;
	box-sizing: border-box;
	color: #fff;
	background: #182126 url('../img/screen-1-bg.jpg') center no-repeat;
	background-size: cover;

	.container {
		position: relative;
		&::after {
			content: 'FOR PROFESSIONALS';
			position: absolute;
			top: 50%;
			right: 0;
			font-size: 30px;
			line-height: 30px;
			letter-spacing: 1px;
			color: $font-light;
			transform-origin: right;
			transform: rotate(-90deg) translate(25%, -100%);
			opacity: .6; } }

	&-h1 {
		margin: 0 0 55px;
		font-size: 62px;
		line-height: 66px;
		text-transform: uppercase;
		color: $accent; }

	&-list {
		margin: 0 0 70px;
		padding: 0;
		list-style: none;
		&__li {
			margin-bottom: 12px;
			font-size: 24px;
			line-height: 28px;
			text-transform: uppercase;
			&::before {
				content: '';
				display: inline-block;
				vertical-align: middle;
				margin-right: 10px; }
			&:last-of-type {
				margin-bottom: 0; } } }

	.btn {
		margin-right: 40px; } }

@include media-breakpoint-down(xxl) {
	.index-screen-1 {
		padding: 120px 0 50px;
		box {}


		.container {
			&::after {
				font-size: 26px;
				line-height: 26px;
				opacity: .8; } }

		&-h1 {
			font-size: 50px;
			line-height: 54px; }

		&-list {
			margin-bottom: 60px; } } }

@include media-breakpoint-down(xl) {
	.index-screen-1 {
		padding: 70px 0 50px;

		.container {
			&::after {
				font-size: 20px;
				line-height: 20px;
				opacity: .8; } }


		&-h1 {
			margin-bottom: 45px;
			font-size: 45px;
			line-height: 49px; }

		&-list {
			margin-bottom: 50px;
			&__li {
				font-size: 20px;
				line-height: 24px; } } } }

@include media-breakpoint-down(lg) {
	.index-screen-1 {
		padding: 60px 0 40px;

		.container {
			&::after {
				font-size: 18px;
				line-height: 18px;
				opacity: .6; } }


		&-h1 {
			margin-bottom: 35px;
			font-size: 40px;
			line-height: 44px; }

		&-list {
			margin-bottom: 40px;
			&__li {
				font-size: 18px;
				line-height: 22px; } } } }

@include media-breakpoint-down(md) {
	.index-screen-1 {
		padding: 50px 0 30px;
		height: auto;
		min-height: auto;

		.container {
			&::after {
				display: none; } }

		&-h1 {
			margin-bottom: 25px;
			font-size: 30px;
			line-height: 34px; }

		&-list {
			margin-bottom: 30px;
			&__li {
				font-size: 16px;
				line-height: 20px; } } } }


@include media-breakpoint-down(md) {
	.index-screen-1 {
		position: relative;
		padding: 30px 0 30px;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, .2); }

		.container {
			position: relative;
			z-index: 2; }

		&-h1 {
			margin-bottom: 25px;
			font-size: 30px;
			line-height: 34px; }

		&-list {
			margin-bottom: 20px;
			&__li {
				font-size: 16px;
				line-height: 20px;
				&::before {
					margin-top: -2px;
					font-size: 14px;
					line-height: 14px;
					font-weight: 700; } } }

		.btn:not(:last-of-type) {
			margin-bottom: 10px; } } }

@include media-breakpoint-down(xs) {
	.index-screen-1 {
		position: relative;
		&::before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background-color: rgba(0, 0, 0, .4); }

		&-h1 {
			margin-bottom: 20px;
			font-size: 24px;
			line-height: 28px; }

		&-list {
			margin-bottom: 20px;
			&__li {
				font-size: 16px;
				line-height: 20px;
				&::before {
					margin-top: -2px;
					font-size: 14px;
					line-height: 14px;
					font-weight: 700; } } }

		.btn:not(:last-of-type) {
			margin-bottom: 10px; } } }
