.lines {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	margin-left: 3px;
	margin-top: -4px;
	width: 15px;
	height: 2px;
	background-color: $font-light;
	box-shadow: 0 -6px 0 0 $font-light, 0 6px 0 0 $font-light;
	transition: .25s;
	&::before,
	&::after {
		content: '';
		position: absolute;
		left: 0;
		width: 100%;
		height: 3px;
		background-color: transparent;
		transition: .25s; }

	&::before {
		transform: rotate(45deg); }

	&::after {
		transform: rotate(-45deg); } }

.link {
	&_accent {
		color: $font-light;
		text-decoration: none;
		transition: .25s;
		&:hover {
			color: $accent;
			text-decoration: underline; } } }

.pagination {
	list-style: none;
	&__li {
		margin: 0 5px;
		font-weight: 700;
		&_active {
			.pagination__link {
				text-decoration: none;
				color: #fff;
				background-color: $accent;
				&:hover {
					background-color: $accent-dark; } } } }
	&__link {
		display: block;
		min-width: 54px;
		text-align: center;
		font-size: 25px;
		line-height: 54px;
		font-weight: 400;
		color: $font-dark;
		background-color: $font-light;
		transition: .25s;
		&:hover {
			text-decoration: none;
			color: #fff;
			background-color: $accent; } } }

.unmarked-list {
	margin: 0;
	padding: 0;
	list-style: none;
	column-count: 2;
	&_upper {
		text-transform: uppercase; }
	&_2-col {
		column-count: 2; }
	&__li {
		position: relative;
		margin-bottom: 20px;
		padding-left: 36px;
		font-size: 20px;
		color: $font-light;
		&::before {
			content: '';
			position: absolute;
			top: 9px;
			left: 0;
			width: 15px;
			height: 15px;
			background-color: #5a6774;
			border-radius: 50%; } } }

@include media-breakpoint-down(xxl) {
	.unmarked-list__li {
		padding-left: 30px;
		font-size: 18px;
		line-height: 22px;
		&::before {
			top: 6px;
			width: 12px;
			height: 12px; } } }

@include media-breakpoint-down(xl) {
	.pagination__link {
		min-width: 50px;
		line-height: 50px; }

	.unmarked-list__li {
		margin-bottom: 15px;
		font-size: 16px;
		line-height: 20px; } }

@include media-breakpoint-down(lg) {
	.pagination__link {
		min-width: 45px;
		font-size: 22px;
		line-height: 45px; }

	.unmarked-list__li {
		margin-bottom: 10px;
		padding-left: 20px;
		font-size: 14px;
		line-height: 18px;
		&::before {
			top: 4px;
			width: 10px;
			height: 10px; } } }


@include media-breakpoint-down(md) {
	.pagination__link {
		min-width: 40px;
		font-size: 20px;
		line-height: 40px; }

	.unmarked-list {
		margin-bottom: 20px;
		&__li {
			margin-bottom: 15px;
			padding-left: 18px;
			&::before {
				top: 3px;
				width: 10px;
				height: 10px; } } } }

@include media-breakpoint-down(sm) {
	.pagination__link {
		min-width: 35px;
		font-size: 18px;
		line-height: 35px; } }

@include media-breakpoint-down(xs) {
	.unmarked-list_2-col {
		column-count: 1; } }
